'use strict';

function isAllowedElement(node) {
	if(allowedElements().indexOf(node.tagName) == -1) {
		return false;
	}
	return true;
}

function allowedElements() {
  return ["IMG","CANVAS","TABLE"];
}


module.exports = {
  isAllowedElement: isAllowedElement,
  allowedElements: allowedElements,
};